import * as React from "react"
import Layout from "../components/layout"
import Seo from "../components/seo"
import NumberFinder from '../components/NumberFinder'
import useAuth from "../hooks/userAuth";
import { StaticImage } from "gatsby-plugin-image"
import { Form, Button, InputGroup } from '@themesberg/react-bootstrap';
import Parse from '../vendor/Parse'
import Header from '../components/Head'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch, faEye } from "@fortawesome/free-solid-svg-icons";
import { navigate } from 'gatsby';
import OutlinedButton from '../components/Button'

const IndexPage = () => {
  const { isLogin } = useAuth()
  const [query, setQuery] = React.useState("")

  const handleMonitorButton = async () => {
    const res = await Parse.Cloud.run('updateUserConfig', {
      key: "monitor",
      value: query
    })
  }

  const handleSearchButton = async () => {
    navigate(`/?q=${query?.trim()}&p=0`)
  }
  return <Layout afterSearch={<></>} >
    <Header />
    <div className="d-xl-flex justify-content-between flex-wrap flex-md-nowrap align-items-center py-2 flex-column text-center" >
      <Form className="">
        <Form.Group id="topbarSearch">
          <InputGroup className="input-group-merge search-bar">
            <InputGroup.Text><FontAwesomeIcon icon={faSearch} /></InputGroup.Text>
            <Form.Control type="text" name="search" placeholder="Search" value={query} onChange={(e) => setQuery(e.target.value)} />
            <Button onClick={handleSearchButton} variant="primary" size="sm" className="me-2">
              Find
            </Button>
          </InputGroup>
        </Form.Group>
      </Form>
    </div>
    <div className="text-center">To see the number, be our mobile number reseller(almost free).<br />
      <a style={{ color: "#25D366" }} href={`https://api.whatsapp.com/send?phone=6587413159&text=I want to be a reseller`}>whatsapp</a> us.

    </div>
    <NumberFinder onQuery={q => setQuery(q)} />
    <div style={{ textAlign: "center" }}>
      <a href={`https://api.whatsapp.com/send?phone=6587413159&text=I want to sign up a circles.life plan`}>
        <StaticImage
          src="../assets/img/ads.jpg" // Path relative to the component
          alt="Description of image"
          placeholder="blurred"
          layout="constrained"
          width={400}
          style={{ margin: "auto" }}
        />
      </a>
    </div>
  </Layout >
}

/**
 * Head export to define metadata for the page
 *
 * See: https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-head/
 */
export const Head = () => <Seo title="Home" />

export default IndexPage
